.home {
    margin-left: 150px;
    text-align: center;
}

.home img {
    height: 250px;
    width: 250px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
}

.home .clickable {
    float: left;
    margin-left: 500px;
}
.projectBox {
	border-radius: 10px;
	margin: 10px 1.6%;
	overflow: hidden;
	position: relative;
    width: 30%;
    font-family: 'Roboto', sans-serif;
}

.projectOverview {
    position: relative;
	text-align: center;
	width: 100%;
	height: 100%;
}

.overview {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    object-fit: cover;
	-webkit-transition: -webkit-transform 200ms ease-in;
	transition: -webkit-transform 200ms ease-in;
	transition: transform 200ms ease-in;
	transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
	height: 50%;  	
	width: 60%;
    background-color: rgba(255, 255, 255, 0.85);
    border-radius: 10px;
    margin: 0px;
    padding: 10px;
	font-size: 10px;
	overflow: hidden;
}

.projectBox img {
	object-fit: cover;
	-webkit-transition: -webkit-transform 200ms ease-in;
	transition: -webkit-transform 200ms ease-in;
	transition: transform 200ms ease-in;
	transition: transform 200ms ease-in, -webkit-transform 200ms ease-in;
	height: 100%;  	
    width: 100%;
}

.projectContent {
	background-color: rgba(0, 0, 0, 0.5);
	display: flex;
	justify-content: center;
	align-items: center;
	flex-direction: column;
	color: #fff;
	position: absolute;
	top: 0;
	left: 0;
	opacity: 0;
	-webkit-transition: opacity 200ms ease-in-out;
	transition: opacity 200ms ease-in-out;
	width: 100%;
	height: 100%;
}

.projectBox:hover .projectContent {
	opacity: 1;
}

.projectBox:hover .overview {
	opacity: 0;
}

.projectBox:hover img {
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}

.projectContent p {
	color: #fff;
	font-size: 15px;
	padding: 10px;
	text-align: center;
}

.projectBox p {
	line-height: 24px;
	margin: 0;
}

.projectContent a {
	background-color: #fff;
	border-radius: 3px;
	color: #000;
	margin-top: 20px;
	padding: 7px 15px;
	text-decoration: none;
}

@media (max-width: 960px) {
	.projectBox {
		width: 48%;
		margin: 15px 1%;
	}
}

@media (max-width: 768px) {
	.projectBox {
		width: 100%;
		margin: 15px 0;
	}
}

.fullLine {
    border-bottom: 1px solid grey;
    box-shadow: 1px 1px 1px grey;
    margin: 5px;
    margin-right: 20px;
    margin-left: 20px;
}

.projectPage {
	margin-left : 250px;
}

.title {
	margin-left: 50px;
}

.project {
	display: flex;
	flex-wrap: wrap;
	max-width: 1120px;
	margin: 20px;
}

.fullLine {
    border-bottom: 1px solid black;
    box-shadow: 1px 1px 1px black;
    margin-left: 20px;
}
.timelineBox {
    display: flex;
    justify-content: flex-end;
    padding-right: 30px;
    position: relative;
    margin: 10px 0;
    width: calc(50% - 27px);
}

.timelineBox:nth-child(odd) {
    align-self: flex-end;
    justify-content: flex-start;
    padding-left: 30px;
    padding-right: 0;
}

.boxTitle {
    float: left;
    margin-top: 10px;
    margin-bottom: 10px;
}

.boxTitle .logo {
    max-width: 150px;
    max-height: 70px;
}

.content {
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
    border-radius: 5px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 15px;
    position: relative;
    width: 400px;
    max-width: 70%;
    text-align: right;
}

.content::after {
    content: ' ';
    background-color: #fff;
    box-shadow: 1px -1px 1px rgba(0, 0, 0, 0.2);
    position: absolute;
    right: -7.5px;
    top: calc(50% - 7.5px);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    width: 15px;
    height: 15px;
}

.timelineBox:nth-child(odd) .content {
    text-align: left;
    align-items: flex-start;
}

.timelineBox:nth-child(odd) .content::after {
    right: auto;
    left: -7.5px;
    box-shadow: -1px 1px 1px rgba(0, 0, 0, 0.2);
}

.content .tag {
    color: #fff;
    font-size: 12px;
    font-weight: bold;
    top: 5px;
    left: 5px;
    letter-spacing: 1px;
    padding: 5px;
    margin-top: 2px;
    position: absolute;
    text-transform: uppercase;
}

.boxTitle .img {
    left: auto;
    float: left;
}

.timelineBox:nth-child(odd) .content .tag {
    left: auto;
    right: 5px;
}

.content time {
    color: #777;
    font-size: 12px;
    font-weight: bold;
}

.content h2 {
    font-size: 16px;
    line-height: 24px;
    margin: 0px;
    max-width: 250px;
}

.content p {
    font-size: 13px;
    line-height: 15px;
    margin: 15px 0;
    margin-top: 0px;
    max-width: 250px;
}

.content a {
    font-size: 14px;
    font-weight: bold;
    text-decoration: underline;
    color: black;
}

.content a::after {
    content: ' ►';
    font-size: 12px;
}

.content .circle {
    background-color: #fff;
    border: 3px solid #e17b77;
    border-radius: 50%;
    position: absolute;
    top: calc(50% - 10px);
    right: -40px;
    width: 20px;
    height: 20px;
    z-index: 100;
}

.timelineBox:nth-child(odd) .content .circle {
    right: auto;
    left: -40px;
}

.linkDiv {
    margin-top: 5px;
}

.label {
    background-color: #eaeaea;
    padding: 2px;
    margin-right: 5px;
    text-transform: capitalize;
    border: 1px solid #cccccc;
    border-radius: 3px;
}

.labelDiv {
    margin-bottom: 5px;
    font-size: 10px;
}

.aboutPage {
    margin-left: 250px;
}

.title {
